sor:pointer}.input-styling input[type='range']{height:90px;width:2px;background:#999;-webkit-appearance:none;border:none;outline:0}.input-styling input[type=range]::-webkit-slider-thumb{-webkit-appearance:none;height:10px;width:10px;border-radius:30px;background:#050303;cursor:pointer}.input-styling input[type='range']{width:90px;margin:0 14px;-webkit-appearance:none;background-color:#999;height:2px;border:none;outline:0}@media (max-width: 620px){.input-styling input{display:none}}.artist-smaller{padding:0 20px;font-size:18px !important;justify-content:start !important;padding-top:6px}.song-name-smaller{padding:0 20px;font-size:21px !important;justify-content:start !important;padding-bottom:0 !important}

.pinned-to-bottom{display:flex;background:#D7D5D3 !important;height:100px !important;box-shadow:0px 0px 10px 0px rgba(0,0,0,0.2)}.img-smaller{position:static !important;height:86px;width:86px}.cover-container-small{align-items:center !important;height:100% !important;width:25% !important;justify-content:start !important;padding:0 40px;cursor:pointer}@media (max-width: 1000px){.cover-container-small{padding:0 40px 0 55px}}@media (max-width: 730px){.cover-container-small{display:none !important}}.small-title{width:100%}

