.loader-wrapper {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba(0, 0, 0, 0.5);
    z-index: 4;
    height: 100%;

    .loader {
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
    }
}